.calculator {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #e8e8e8;
    padding: 40px 20px;
    border-radius: 20px;
    flex-direction: column;
    gap: 20px;
    max-width: 900px;
    margin: 0 auto;
}

.inputs, .result-box {
    width: 100%;
    text-align: left;
    background: #e8e8e8;
    padding: 30px;
    border-radius: 10px;
}

select, input, button {
    width: 100%;
    padding: 15px;
    margin: 10px 0;
    font-size: 18px;
    border: 1px solid #ccc;
    border-radius: 10px;
    background: #f8f8f8;
    color: #333;
}

button {
    background: #203e6f;
    color: white;
    font-weight: bold;
    font-size: 20px;
    transition: 0.3s;
    cursor: pointer;
}

button:hover {
    background: #172d4a;
    transform: scale(1.05);
}

.result-box {
    background: #203e6f;
    color: white;
    border-radius: 15px;
    padding: 40px 30px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.result-title {
    font-size: 28px;
    font-weight: bold;
    margin-bottom: 10px;
}

.result-value {
    font-size: 50px;
    font-weight: bold;
}

.result-note {
    font-size: 16px;
    margin-top: 20px;
    color: #ddd;
    line-height: 1.5;
}